h1 {
  text-align: center;
  color: blue;
}

.cardWrap {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 20px;
  justify-content: center;
  margin: 0 auto;
  max-width: 800px;
}

.cardItem {
  background-color: rgb(255, 243, 80);
  color: blue;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: baseline;
  border-radius: 5px;
  box-shadow: 3px 3px 10px #6a6a6a;
}

.cardList {
  padding: 0;
}

.cardList li {
  list-style: none;
}

.btnWrap {
  text-align: center;
  margin: 20px;
}

.btnWrap button {
  margin: 0 10px;
  padding: 5px 20px;
  cursor: pointer;
  background-color: blue;
  color: #fff;
  font-weight: bold;
  border-radius: 5px;
  box-shadow: 1px 1px 3px #6a6a6a;
}

.btnWrap button:hover {
  box-shadow: none;
  transform: translate(1px, 1px);
  transition: all .2s;
}